.navbar {
    display: flex;
    align-items: left;
    color: #FF5A5F;
    font-size: 1.5em;
    box-shadow: 0px 5px 30px rgba(0 ,0 ,0 , 0.5);
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 36px;
}

.navbar > b {
    padding-top: 5px;
    padding-left: 2px;
}

.App {
    position: relative;
    width: 450px;
    height: 800px;
    background: #ffffff;
    border-radius: 8.95px;
    box-shadow: 0px 5px 30px rgba(0 ,0 ,0 , 0.5);
    margin-left: 35%;
}

.grey {
    color: #918E98;
}

.bold {
    font-weight: bold;
}

.img {
    align-items: center;
    padding-top: 5px;
    display: flex;
}

.af-img {
    text-align: left;
    padding-left: 20px;
    font-size: 16px;
    line-height:17.6px;
}

.img > img {
    max-width: 400px;
    align-self: center;
    padding-left: 25px;
}

.card{
    width: 175px;
    font-size: 13px;
    padding-left: 20px;
    min-width: 175px;
    position: relative;
    
}

.card > p {
    text-align: left;
    padding-left: 2px;
}

.card--img {
    width: 100%;
    border-radius: 9px;
    max-height: 235px;
}

.card--stats {
    display: flex;
    align-items: center;
}

.card--star {
    height: 14px;
}

.card-row {
    flex: 0 0 auto;
    display: flex;
    flex-shrink: 0;
    overflow: scroll;
    text-overflow: ellipsis;
}

.card--badge {
    position: absolute;
    top: 6px;
    left: 20px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 3px;
    font-weight: bold;
}